
import { defineComponent } from "vue";
import enumProp from "../helpers/enum-prop";
import {
  BaseTextFieldEventChange,
  baseTextFieldEvents,
  baseTextFieldPropsSizes,
} from "./BaseTextField.types";

export default defineComponent({
  name: "BaseTextField",
  props: {
    fullWidth: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "",
    },
    size: {
      ...enumProp("medium", baseTextFieldPropsSizes),
      default: "medium",
    },
    value: {
      type: String,
      default: "",
    },
  },
  emits: [...baseTextFieldEvents],
  data() {
    return {
      dataFullWidth: this.fullWidth,
      dataPlaceholder: this.placeholder,
      dataSize: this.size,
      dataValue: this.value,
    };
  },
  computed: {
    classes(): string {
      const c = ["base-text-field", `base-text-field--${this.dataSize}`];

      if (this.dataFullWidth) {
        c.push("base-text-field--full-width");
      }

      return c.join(" ");
    },
  },
  watch: {
    dataValue(newValue, oldValue) {
      const change: BaseTextFieldEventChange = {
        newValue,
        oldValue,
      };
      this.$emit("change", change);
    },
  },
});
